import React from 'react';
import { useParams } from 'react-router-dom'
import 'antd/dist/antd.css';
import { Typography, Descriptions, Alert, PageHeader, Divider } from 'antd';
import axios from 'axios';
const { Paragraph } = Typography;

export default function ServerPsw(props) {
    let { server_ip } = useParams();

    return (
        <div>
            <PageHeader
                ghost={false}
                title=""
                subTitle={`:${server_ip}`}
            >
            </PageHeader>
            <Divider />
            <ServerPswShow server_ip={server_ip} />
        </div>
    );
}

class ServerPswShow extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id: ""
        }
    }

    componentDidMount() {
        this.fetch();
    }

    fetch() {
        axios.post('/api',
            {
                api: 'psw_check',
            }).then(data => {
                console.log(data.data.id)
                this.setState({
                    id: data.data.id
                })
            });
    }

    render() {
        const { id } = this.state;
        return (
            <div>
                <Alert
                    message=""
                    description=""
                    type="info"
                    showIcon
                />
                <br />
                <Descriptions
                    title=""
                    bordered
                    column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
                >
                    <Descriptions.Item label="">
                        <Paragraph copyable>{id}</Paragraph>
                    </Descriptions.Item>
                </Descriptions>
            </div>
        );
    }
}
